import { useQuery, QueryClient } from "react-query";
import { md_giftsKeys, normalizeKeys } from "../constants/md-giftsKeys";
import { supabase } from './supabaseClient';
import { getValidatedDataSupabase } from "../constants/validations";

const CACHE_KEY = 'md-gifts_cache';
const CACHE_TIME = 5 * 60 * 1000; // 5 minutes in milliseconds

const getDataFromCache = () => {
  const cachedData = localStorage.getItem(CACHE_KEY);
  if (cachedData) {
    const { data, timestamp } = JSON.parse(cachedData);
    if (Date.now() - timestamp < CACHE_TIME) {
      return data;
    }
  }
  return null;
};

const setDataToCache = (data: any) => {
  const cacheData = {
    data,
    timestamp: Date.now(),
  };
  localStorage.setItem(CACHE_KEY, JSON.stringify(cacheData));
};

const useFetchGiftsAction = async () => {
  const cachedData = getDataFromCache();
  if (cachedData) {
    return cachedData;
  }

  try {
    const { data: gifts, error } = await supabase
    .from('md_gifts_db')
    .select(`
      *,
      obtain:md_gifts_obtain_db (
        obtainEn,
        obtainRu
      )
    `)
    .order('Keyword', { ascending: true })
    .order('sin', { ascending: true })
    .order('tier', { ascending: true })
    .order('imgUrl-id', { ascending: true });
 
    if (error) {
      throw error;
    }

    if (!gifts) {
      throw new Error('No data received');
    }

    const result = getValidatedDataSupabase(gifts, md_giftsKeys, normalizeKeys);
    setDataToCache(result);
    return result;
  } catch (error) {
    console.error('Error fetching data:', error);
    throw error;
  }
};

export const useFetchMDGifts = () => {
  return useQuery("md-gifts", useFetchGiftsAction, {
    staleTime: CACHE_TIME,
    cacheTime: Infinity,
    refetchOnWindowFocus: false,
    retry: 8,
    retryDelay: (attempt) =>
      Math.min(attempt > 1 ? 2 ** attempt * 1000 : 1000, 30 * 1000),
    onError: (error) => {
      console.error("Error fetching md-gifts:", error);
    },
  });
};

// Create a custom QueryClient to handle cache persistence
export const createQueryClient = () => {
  const queryClient = new QueryClient({
    defaultOptions: {
      queries: {
        staleTime: CACHE_TIME,
        cacheTime: Infinity,
      },
    },
  });

  // Hydrate the cache from localStorage on client-side
  if (typeof window !== 'undefined') {
    const cachedData = getDataFromCache();
    if (cachedData) {
      queryClient.setQueryData("md-gifts", cachedData);
    }
  }

  return queryClient;
};